<template>
  <div class='massmedia wrapper'>
    <div class='mm__title indigo-title'>
         
    </div>
    <VueSlickCarousel class="mm-slider" v-bind='settings'>
      <a target="_blank" href="https://google.com/" class="mm-slider__item">
        <img src="../../src/assets/img/mm1.svg" alt="">
      </a>
      <a target="_blank" href="https://google.com/" class="mm-slider__item">
        <img src="../../src/assets/img/mm2.svg" alt="">
      </a>
      <a target="_blank" href="https://google.com/" class="mm-slider__item">
        <img src="../../src/assets/img/mm3.svg" alt="">
      </a>
      <a target="_blank" href="https://google.com/" class="mm-slider__item">
        <img src="../../src/assets/img/mm4.gif" alt="">
      </a>
      <template #prevArrow="arrowOption">
        <div class="custom-arrow">
          <img src="../../src/assets/img/arrowLeft.svg">
          {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
        </div>
      </template>

      <template #nextArrow="arrowOption">
        <div class="custom-arrow">
          <img src="../../src/assets/img/arrowRight.svg">
          {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
        </div>
      </template>
    </VueSlickCarousel>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: 'MassMedia',
  components: { VueSlickCarousel },
  data () {
    return {
      settings: {
        dots: true,
        infinite: true,
        centerMode: true,
        centerPadding: '20px',
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true,
        autoplay: true,
        autoplaySpeed: 3000,
        responsive: [
          {
            breakpoint: 767,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 1
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          }
        ]
      }
    }
  }
}
</script>

<style>
.massmedia{
  position: relative;
  margin-bottom: 80px;
}
.massmedia::after{
  content: '';
  width: 100%;
  height: 100%;
  background-color: #F8F8F8;
  z-index: -1;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-clip-path: polygon(0 0, 100% 14%, 100% 100%, 0 86%);
    clip-path: polygon(0 0, 100% 14%, 100% 100%, 0 86%);
}
.mm__title{
  text-align: center;
  width: 100%;
  padding: 125px 0 48px;
}
.mm-slider{
  padding-bottom: 200px;
  box-sizing: border-box;
}
.mm-slider__item{
  display: block;
  width: auto !important;
  margin: 0 50px;
  outline: none;
}
.mm-slider__item img{
  height: 67px;
  margin: 0;
  display: inline !important;
}
.massmedia .slick-slide{
  opacity: .65;
  transition: .6s;
}
.massmedia .slick-slide:hover{
  opacity: .9;
}
.massmedia .slick-slide.slick-active{
  opacity: 1;
}
.massmedia .slick-dots{
  bottom: 130px;
}
.massmedia .slick-prev{
  left: 200px;
}
.massmedia .slick-arrow{
  z-index: 100;
}
.massmedia .custom-arrow::before{
  display: none;
}
.massmedia .slick-next{
  right: 200px;
}
.massmedia .slick-prev, .slick-next{
  width: auto;
  bottom: 122px !important;
  transition: .4s;
  opacity: .8;
}
.massmedia .slick-prev:hover, .slick-next:hover{
  opacity: 1;
}
@media (max-width: 991px){
  .massmedia .slick-next{
    right: 100px;
  }
  .massmedia .slick-prev{
    left: 100px;
  }
  .mm-slider__item img{
    height: 42px;
  }
}
@media (max-width: 769px){
  .massmedia .slick-next{
    right: 40px;
  }
  .massmedia .slick-prev{
    left: 40px;
  }
  .mm-slider__item{
    margin: 0 24px;
  }
  .massmedia{
    margin-bottom: 40px;
  }
  .mm-slider{
    padding-bottom: 90px;
  }
  .massmedia .slick-dots{
    bottom: 50px;
  }
  .massmedia .custom-arrow{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .massmedia .custom-arrow img{
    display: block;
    width: 40px;
  }
  .massmedia .slick-dots li button:before{
    font-size: 10px;
  }
  .mm__title{
    text-align: center;
    width: 100%;
    padding: 40px 0 28px;
  }
}
@media (max-width: 525px){
  .mm-slider__item img{
    height: 28px;
  }
  .massmedia .slick-dots li button:before{
    font-size: 8px;
  }
  .massmedia .slick-dots li{
    margin: 0;
  }
}

</style>
