<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: 'Review',
  components: { VueSlickCarousel },
  data () {
    return {
      settingsNav: {
        dots: true,
        // infinite: false,
        centerMode: true,
        // initialSlide: 2,
        focusOnSelect: true,
        centerPadding: '20px',
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true,
        autoplay: false,
        autoplaySpeed: 3000,
        // infinite: false,
        asNavFor: this.$refs.review,
        responsive: [
          {
            breakpoint: 767,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 1,
              dots: false
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
              // initialSlide: 2
            }
          }
        ]
      },
      settingsReview: {
        dots: false,
        arrows: false,
        // initialSlide: 2,
        slidesToShow: 1,
        slidesToScroll: 1,
        // infinite: false,
        asNavFor: this.$refs.nav
      }
    }
  },
  methods: {
    changeNav (currentPosition, nextPosition, event, slick, currentSlide, nextSlide) {
      this.$refs.review.goTo(nextPosition)
    },
    changeReview (currentPosition, nextPosition, event, slick, currentSlide, nextSlide) {
      this.$refs.nav.goTo(nextPosition)
    }
  },
  mounted () {
    // syncSliders()
  }
}
</script>

