<template>
    <header id="header">
      <div class="header__logo">
        <router-link to="/">
          <img alt="Vue logo" class="logo" src="../../src/assets/img/logo-black.svg">
        </router-link>
      </div>
      <div id="nav" class="header__links">
        <router-link to="/about"> </router-link>
        <router-link to="/services"></router-link>
        <router-link to="/vacancies"></router-link>
        <router-link to="/candidates"></router-link>
        <router-link to="/school"></router-link>
        <router-link to="/blog"></router-link>
      </div>
      <div class="header__control">
        <a class="header__control-mail" href="mailto:indigo@gmail.com"> <img src="../../src/assets/img/icon-mail.svg">  </a>
        <a class="header__control-lang">RU</a>
      </div>
      <div @click="toggle" class="header-mob__burger">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div class="menu-mob" v-bind:class="{ show: open, 'closed': !open }">
        <div id="nav-mob" class="menu-mob__nav-mob" @click="open = !open">
          <router-link to="/about"> </router-link>
          <router-link to="/services"></router-link>
          <router-link to="/vacancies"></router-link>
          <router-link to="/candidates"></router-link>
          <router-link to="/school"></router-link>
          <router-link to="/blog"></router-link>
        </div>
        <div class="menu-mob__footer">
          <div class="menu-mob__langs">
              <a class="header__control-lang active">RU</a>
              <a class="header__control-lang">UA</a>
              <a class="header__control-lang">EN</a>
          </div>
          <div class="menu-mob__contact">
              <a href="tel:+380636334711">+38 (063) 633 47 11</a>
              <a href="mailto:info@indigo.co.ua">info@indigo.co.ua</a>
          </div>
        </div>
      </div>
    </header>
</template>

<script>
export default {
  name: 'HeaderTemplate',
  data () {
    return {
      open: false
    }
  },
  methods: {
    toggle () {
      this.open = !this.open
      if (this.open) {
        document.getElementById('body').classList.add('noscroll');
      } else {
        document.getElementById('body').classList.remove('noscroll');
      }
    }
  }
}
</script>

<style>
#header {
  height: 92px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 128px;
}

#nav{
  display: flex;
}

.logo{
  height: 45px;
  position: relative;
  z-index: 5000;
}

#nav a {
  display: block;
  padding: 38px 24px;
  color: #4F4F4F;
  font-weight: 500;
  font-size: 14px;
  text-decoration: none;
  transition: .4s;
}
#nav a:hover{
  color: black;
}
#nav a.router-link-exact-active {
  color: var(--indigo);
}
.header__control{
  display: flex;
  height: 40px;
}
.header__control-mail{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 22px;
  background: var(--indigo);
  font-weight: 500;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
  transition: .4s;
}
.header__control-mail:hover{
  opacity: .85;
}
.header__control-mail img{
  margin-right: 14px;
  transform: translateY(1px);
  height: 12px;
}
.header__control-lang{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 8px;
  margin-left: 18px;
  font-weight: 500;
  font-size: 14px;
  border: 2px solid #F2F2F2;
  box-sizing: border-box;
  cursor: pointer;
  transition: .4s;
}
.header__control-lang:hover{
  background: #F2F2F2;
}
.menu-mob__langs{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}
.menu-mob__langs .header__control-lang{
    margin: 0 8px;
}
.header__control-lang.active{
    background: #F2F2F2;
}
.header-mob__burger {
  position: relative;
  display: none;
  width: 62px;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-right: -25px;
  z-index: 5000;
}
.header-mob__burger span{
  display: block;
  margin-bottom: 4px;
  width: 18px;
  height: 2px;
  border-radius: 2px;
  background: var(--indigo);
}
.menu-mob{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 1000;
  left: 100vw;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding-top: 100px;
  transition: .6s;
  box-sizing: border-box;
}
#nav-mob{
  width: 100%;
  height: auto;
}
#nav-mob a {
  display: block;
  width: 100%;
  text-align: center;
  padding: 18px 0px;
  color: #4F4F4F;
  font-weight: 500;
  font-size: 18px;
  text-decoration: none;
  transition: .4s;
}
#nav-mob a:hover{
  color: black;
}
#nav-mob a.router-link-exact-active {
  color: var(--indigo);
}
.show{
  left: 0;
}
.menu-mob__footer{
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px;
}
.menu-mob__contact{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.menu-mob__contact a{
    display: block;
    font-weight: 500;
    font-size: 18px;
    line-height: 140.62%;
    color: #000;
    text-decoration: none;
    margin-bottom: 8px;
}
@media (max-width: 1440px){
  #header {
    padding: 0 90px;
  }
}

@media (max-width: 1365px){
  #header {
    padding: 0 40px;
  }
}

@media (max-width: 1279px){
  #header {
    padding: 0 25px;
    height: 76px;
  }

  .logo{
    height: 35px;
  }

  #nav a {
    display: block;
    padding: 32px 18px;
    color: #4F4F4F;
    font-weight: 500;
    font-size: 12px;
    text-decoration: none;
    transition: .4s;
  }

  .header__control a{
    font-size: 12px;
  }
  .header__control-mail img{
    height: 11px;
  }
}

@media (max-width: 991px){
  #nav{
    display: none;
  }

  .header__control{
    display: none;
  }

  .header-mob__burger{
    display: flex;
  }
}
@media (max-width: 374px){
    .menu-mob__nav-mob a{
        font-size: 14px;
        padding: 16px 0px;
    }
}
@media (max-height: 620px){
    .menu-mob__nav-mob a{
        font-size: 14px ;
        padding: 14px 0px !important;
        /* background: red; */
    }
    .menu-mob__langs{
        margin-bottom: 24px;
    }

    .menu-mob__contact a{
        font-size: 14px;
    }

    .menu-mob__footer{
        padding: 14px;
    }
}
@media (max-height: 400px){
    .menu-mob__nav-mob a{
        font-size: 12px !important;
        padding: 8px 0px !important;
        /* background: red; */
    }
    .menu-mob__contact a{
        font-size: 10px;
    }
    .menu-mob__langs{
        margin-bottom: 16px;
    }
    .menu-mob__langs .header__control-lang{
        font-size: 10px !important;
    }
}
</style>
