<template>
  <div id="app">
    <HeaderTemplate/>
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    <FooterTemplate/>
  </div>
</template>

<script>
import FooterTemplate from '@/components/FooterTemplate.vue'
import HeaderTemplate from '@/components/HeaderTemplate.vue'

export default {
  name: 'app',
  data () {
    return {
      crayfish: true
    }
  },
  components: {
    HeaderTemplate,
    FooterTemplate
  },
  mounted () {
    window.scrollTo(0, 0);
  }
}
</script>

<style>

#app {
  color: black;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  transition: opacity .4s ease;
}

.fade-leave-active {
  transition: opacity .4s ease;
  opacity: 0;
}

</style>
